.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;

}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: #171717;
    font-size: 38px;
    font-weight: 600;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap:50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    color: #515151;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    font-size: 23px;
    cursor: pointer;
}
.nav-menu li hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius:10px;
    background: #ff4141;
}
.login-button{
    display: flex;
    align-items: center;
    gap: 45px;
}
.login-button button{
    width: 150px;
    height: 50px;
    outline: none;
    border: 1px solid #7a7a7a;
    color: #515151;
    border-radius: 75px;
    font-size: 20px;
    font-weight: 500;
    background-color: white;
    cursor: pointer;
}
.login-button button:active{
    background-color: #f3f3f3;
} 
.cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius:11px ;
    font-size: 14px;
    background-color: #ff4141;
    color:white;
}

@media (max-width:748px){
    .navbar{
        padding: 10px;
    }
}