.offers{
    width: 65%;
    height: 70vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-top: 200px;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
}
.offers_left{
    margin-top: 70px;
    align-items: center;
}
.offers_left h1{
    color: #171717;
    font-size: 70px;
}
.offers_left p{
    color: #171717;
    font-size: 22px;
    
}
.offers_left button{
    width: 282px;
    height: 70px;
    border-radius:35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offers_right{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 140px;
}