.Newletter{

    width: 65%;
    height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    background: linear-gradient(180deg, #fde1ff 0% ,#e1ffea22 60%);
    gap: 30px;
}
.Newletter h1{
    color: #454545;
    font-size: 50px;
    font-weight: 600;
}
.Newletter p{
    color: #454545;
    font-size:20px ;
}
.Newletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid #e3e3e3;
}
.Newletter input{
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: poppins;
    font-size: 16px;

}
.Newletter button{
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}